A;EACE,YAAY;EACZ,YAAY;EACZ,iBAAiB;EACjB,2BAA2B;EAC3B,4CAA4C;EAC5C,sCAAsC;EACtC,kBAAkB;EAClB,mBAAmB;AACrB;AACA;EACE,yBAAyB;EACzB,gCAAgC;EAChC,8CAA8C;EAC9C,mBAAmB;AACrB;AACA,aAAa;;AAEb,WAAW;AACX,UAAU;AACV;EACE,wCAAwC;EACxC,aAAa;EACb,SAAS;EACT,uBAAuB;EACvB,mBAAmB;EACnB,4CAA4C;EAC5C,2BAA2B;EAC3B,iBAAiB;EACjB,gBAAgB;EAChB,aAAa;AACf;AACA;EACE,WAAW;EACX,yBAAyB;AAC3B;AACA;EACE,0BAA0B;EAC1B,mBAAmB;EACnB,8BAA8B;AAChC;AACA,aAAa","sourcesContent":["/* Root and Defaults*/\n/*#region*/\n@font-face {\n  font-family: 'roboto-mono';\n  src: url('../fonts/robotomono-variablefont_wght-webfont.woff2'),\n       url('../fonts/robotomono-variablefont_wght-webfont.woff');\n}\n:root {\n  /* Background Colors */\n  --crt-gray: #282828;\n  --crt-gray-compliment: #1a1a1a;\n  /* Primary Colors */\n  --electric-blue:  #9CDCFD;\n  --binary-green:   #33FF00;\n  /* Secondary Colors */\n  --soft-yellow:    #F9D342;\n  --soft-purple:    #DDB6D8;\n  /* Shadows */\n  --margin-shadow: 0px 0px 25px 2.5px var(--electric-blue);\n  --card-shadow: 0px 0px 5px 1px var(--electric-blue);\n\n  cursor: default;\n}\nh1, h2, h3, h4, h5, h6 {\n  font-family: 'roboto-mono';\n  color: var(--binary-green);\n}\nbutton {\n  cursor: pointer;\n}\ninput[type=\"radio\"],\ninput[type=\"checkbox\"] {\n  cursor: pointer;\n}\nul {\n  list-style-type: none;\n  padding-bottom: 5px;\n}\n\n\n.highlight-purple { \n  color: var(--soft-purple);\n}\n.highlight-yellow {\n  color: var(--soft-yellow);\n}\n.highlight-green {\n  color: var(--binary-green);\n}\n/*#endregion*/\n\n/* Body */\n/*#region*/\nbody#body-container {\n  margin: 1rem 2rem 1rem 2rem;\n  background: black;\n  box-shadow: var(--margin-shadow);\n}\nmain {\n  background-color: var(--crt-gray);\n  border-left: 1.5px solid var(--electric-blue);\n  border-right: 1.5px solid var(--electric-blue);\n  color: var(--electric-blue);\n}\n/*#endregion*/\n\n/* Header and Nav */\n/*#region*/\nheader#header-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  color: var(--binary-green);\n  border: 1.5px solid var(--electric-blue);\n  padding: 10px;\n  background-color: var(--crt-gray-compliment);\n}\n.header-logo {\n  font-weight: 1000;\n  font-size: 3rem;\n}\n.header-slogan {\n  color: var(--electric-blue);\n  font-size: 1.5rem;\n  font-weight: 500;\n  font-family: 'atkinson-hyper-legible';\n  border-bottom: 0.5px solid var(--binary-green);\n}\nnav.header-nav > ul {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  gap:25px;\n  margin-top: 35px;\n}\nnav.header-nav > ul > li > button {\nwidth: 150px;\nheight: 40px;\nfont-size: 1.3rem;\ncolor: var(--electric-blue);\nbackground-color: var(--crt-gray);\nborder: 1.5px solid var(--binary-green);\nborder-radius: 5px;\nfont-weight: bold;\n}\nnav.header-nav > ul > li > button:hover {\n  color: var(--binary-green);\n  border-color: var(--electric-blue);\n  box-shadow: 0px 0px 5px var(--electric-blue);\n  font-weight: bolder;\n  }\n#current-page {\n  color: var(--binary-green);\n  border-color: var(--binary-green);\n  box-shadow: 0px 0px 6px var(--binary-green);\n  font-weight: bolder;\n}\n/*#endregion*/\n\n/* INDEX */\n/*#region*/\n\n/* HERO */\n/*#region*/\nsection#hero-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding-top: 25px;\n  padding-bottom: 25px;\n}\n.hero-image {\n  width: 75vw;\n  max-height: 75vh;\n  position: relative;\n  display: flex;\n  border-radius: 10px;\n  box-shadow: var(--card-shadow);\n}\n.hero-overlay {\n  position: absolute;\n\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  padding: 50px;\n  background-color: #000000f2;\n  \n  color: var(--electric-blue);\n  border: 3px solid var(--binary-green);\n  border-radius: 10px;\n}\n.hero-overlay > h2 {\n  font-weight: 1000;\n}\n.hero-overlay > p {\n  margin-top: 5px;\n}\n.hero-overlay > button {\n  margin-top: 10px;\n  padding: 10px;\n  background-color: var(--crt-gray-compliment);\n  color: var(--elec